* {
  box-sizing: border-box;
}
body {
  overflow: hidden;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: 'Audiowide', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-family: 'Press Start 2P', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #477148;
}

.game {
  height: 100vh;
  width: 63vh;
  max-height: 100vh;
  max-width: 100vw;
  text-align: center;
}
.scoreboard {
  font-size: large;
  height: 16.6666%;
  max-height: 16.6666vh;
}
.title {
  font-size: xx-large;
}
.playArea {
  height: 83.3334%;
  max-height: 83.3334vh;
}
.gameHead {
  color: white;
}
.pickLevel {
  /* height: 75vh; */
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
}
button {
  height: 80px;
  width: 24%;
  margin: 1%;
  text-transform: uppercase;
  font-size: medium;
  font-weight: 800;
  border-radius: 50%;
  background: linear-gradient(to bottom, #efd303, #d18d10);
  color: #282c34;
  border: none;
}
button:hover {
  color: whitesmoke;
  cursor: pointer;
}
button:active {
  transform: translateY(2px);
}

#timer {
  height: auto;
  max-height: 100%;
  font-size: x-large;
}
.playAgain {
  margin: auto;
  font-size: large;
}
